<template>
  <div>
    <div>Windows:</div>
    <div>
      <Checkbox v-model="triggerKeys.win.ctrlKey">Ctrl</Checkbox>
      <Checkbox v-model="triggerKeys.win.altKey">Alt</Checkbox>
      <Checkbox v-model="triggerKeys.win.shiftKey">Shift</Checkbox>
      <span style="margin-left: 25px">按键：</span>
      <Input class="input" :value="triggerKeys.win.key"
             @keydown.native.prevent="triggerKeys.win.key=$event.key" />
    </div>
    <div>Mac:</div>
    <div>
      <Checkbox v-model="triggerKeys.mac.ctrlKey">Ctrl</Checkbox>
      <Checkbox v-model="triggerKeys.mac.altKey">Alt</Checkbox>
      <Checkbox v-model="triggerKeys.mac.shiftKey">Shift</Checkbox>
      <Checkbox v-model="triggerKeys.mac.metaKey">Command</Checkbox>
      <span style="margin-left: 25px">按键：</span>
      <Input class="input" :value="triggerKeys.mac.key"
             @keydown.native.prevent="triggerKeys.mac.key=$event.key" />
    </div>
  </div>
</template>
<script>

export default {
  data() {
    return {};
  },
  computed: {
    triggerKeys() {
      return this.$store.state.setting.triggerKeys;
    },
  },
  created() {
    this.$store.dispatch({ type: 'setting/getConfig' });
  },
};
</script>

